import { Paper } from '@material-ui/core';
import React from 'react';
import Button from '../Button';
import Typography from '../Typography';
import useStyles from './ErrorGameRestartDialog.styles';

export const ErrorGameRestartDialog: React.FC<{ onClick: () => void; score: number; highScore: number }> = ({
  onClick,
  score,
  highScore,
}) => {
  const classes = useStyles();
  return (
    <Paper className={classes.dialog}>
      <Typography className={classes.dialogText}>Game over!</Typography>
      {highScore > score ? (
        <Typography paragraph>Score: {score}</Typography>
      ) : (
        <Typography paragraph>New High Score: {score}</Typography>
      )}
      <Button size="small" tvhVariant="secondary" onClick={onClick}>
        Retry
      </Button>
    </Paper>
  );
};

export default ErrorGameRestartDialog;
