import { GameStyles } from './ErrorGame.styles';
import { PerlinNoiseGenerator } from './perlin-noise-generator';
import { Hitbox, OBSTACLES, PLAYER_CART, PLAYER_CART_HITBOX } from './resources';

const FRAMES_PER_SECOND = 30; // Valid values are 60,30,20,15,10...
// set the mim time to render the next frame
export const FRAME_MIN_TIME = (1000 / 60) * (60 / FRAMES_PER_SECOND) - (1000 / 60) * 0.5;
const LINE_WIDTH = 11;
const ENGINE_RUMBLE = 1;
const displayHitboxes = false;
const drawPerlinLine = false;

interface Obstacle {
  image: HTMLImageElement;
  hitbox: Hitbox;
  x: number;
  y: number;
}

export class Game {
  score: number;
  highScore: number;
  running: boolean;
  private _streetStart;
  private _playerPosition: { x: number; y: number };
  private _movingPlayerX: -1 | 0 | 1;
  private _movingPlayerY: -1 | 0 | 1;
  private _obstacles: Obstacle[];
  private _multiplier: number;
  private _perlinNoiseGenerator: PerlinNoiseGenerator;
  private _noiseLine: { x: number; y: number }[];

  private _canvas: HTMLCanvasElement;
  private _context2d: CanvasRenderingContext2D;
  private _gameStyles: GameStyles;
  private _ticker;
  private _rumble: { x: number; y: number };
  private _seed: number;

  constructor(gameStyles: GameStyles, seed?: number) {
    this._gameStyles = gameStyles;
    this._seed = seed;
  }

  init(canvas: HTMLCanvasElement, context2d: CanvasRenderingContext2D) {
    this._canvas = canvas;
    this._context2d = context2d;
    this._streetStart = 30;
    this._ticker = 0;
    this._noiseLine = [];
    this._rumble = { x: 0, y: 0 };
    this.highScore = 0;
    this._perlinNoiseGenerator = new PerlinNoiseGenerator(
      canvas.width,
      canvas.width - (2 * LINE_WIDTH + PLAYER_CART.width),
      113, // Player cart height
      this._seed
    );
    this._initGame();
  }

  reset() {
    this._initGame();
    this._startGame();
  }

  cycle() {
    this._ticker++;
    if (this.running || this.score === 0) {
      this._calculateGameState();
    }
    this.renderGame();
  }

  arrowLeftDown() {
    this._movingPlayerX = -1;
  }
  arrowLeftUp() {
    if (this._movingPlayerX === -1) {
      this._movingPlayerX = 0;
    }
  }

  arrowRightDown() {
    this._movingPlayerX = 1;
  }
  arrowRightUp() {
    if (this._movingPlayerX === 1) {
      this._movingPlayerX = 0;
    }
  }

  private _startGame() {
    this.running = true;
  }

  private _stopGame() {
    this.running = false;
  }

  private _initGame() {
    this._playerPosition = {
      x: (this._canvas.width - PLAYER_CART_HITBOX.width) / 2,
      y: this._canvas.height - PLAYER_CART_HITBOX.height - 48,
    };
    this._obstacles = [];
    this._multiplier = 1;
    this._movingPlayerX = 0;
    this._movingPlayerY = 0;
    this.running = false;
    this.score = 0;
  }

  private _calculateGameState() {
    this._rumbleCart();
    if (!this.running) {
      return;
    }

    // Update perlin noise
    this._perlinNoiseGenerator.update(this._multiplier);
    this._updatePerlinLine();

    // Update obstacles
    this._updateObstacles();
    if (this._obstacles.some((obstacle) => this._evaluateHit(obstacle, this._playerPosition))) {
      this._stopGame();
      this.highScore = Math.max(this.highScore, this.score);
      return;
    }

    this._streetStart = Math.min(this._streetStart + 1 * this._multiplier, this._canvas.height);
    this.score = this.score + (1 / FRAMES_PER_SECOND) * this._multiplier;
    this._playerPosition = {
      ...this._playerPosition,
      x: Math.max(
        LINE_WIDTH,
        Math.min(this._playerPosition.x + 5 * this._movingPlayerX, this._canvas.width - PLAYER_CART.width - LINE_WIDTH)
      ),
    };
    this._multiplier = this._multiplier * 1.0005;
  }

  private _rumbleCart() {
    switch (this._ticker % 4) {
      case 0:
        this._rumble.x -= ENGINE_RUMBLE;
        break;
      case 1:
        this._rumble.y -= ENGINE_RUMBLE;
        break;
      case 2:
        this._rumble.x += ENGINE_RUMBLE;
        break;
      case 3:
        this._rumble.y += ENGINE_RUMBLE;
        break;
    }
  }

  private _updatePerlinLine() {
    this._noiseLine = this._noiseLine
      .map((noise) => ({
        ...noise,
        y: noise.y + 1 * this._multiplier,
      }))
      .filter((noise) => noise.y < this._canvas.height);

    this._noiseLine.push({
      x: this._perlinNoiseGenerator.x,
      y: 0,
    });
  }

  private _updateObstacles() {
    this._obstacles = this._obstacles
      .map((obstacle) => ({
        ...obstacle,
        y: obstacle.y + 1 * this._multiplier,
      }))
      .filter((obstacle) => obstacle.y < this._canvas.height);
    if (this._obstacles.filter((obstacle) => obstacle.y < PLAYER_CART.height * 1.3).length < 2) {
      const placeOnLeft = this._perlinNoiseGenerator.getRandom() < 0.5;
      const leftSpace = this._perlinNoiseGenerator.x - LINE_WIDTH;
      const rightSpace = this._canvas.width - LINE_WIDTH - this._perlinNoiseGenerator.x;
      const possibleObjects = OBSTACLES.filter(
        (possibleObstacle) => possibleObstacle.hitbox.width < (placeOnLeft ? leftSpace : rightSpace)
      );

      const obstacleIdx =
        Math.round(this._perlinNoiseGenerator.getRandom() * possibleObjects.length) % possibleObjects.length;
      const obstacle = possibleObjects[obstacleIdx];

      if (!obstacle) {
        return;
      }

      const newObstacle: Obstacle = {
        image: obstacle.image,
        hitbox: obstacle.hitbox,
        x: placeOnLeft
          ? LINE_WIDTH + this._perlinNoiseGenerator.getRandom() * (leftSpace - obstacle.image.width)
          : this._perlinNoiseGenerator.x + this._perlinNoiseGenerator.getRandom() * (rightSpace - obstacle.image.width),
        y: -obstacle.image.height,
      };
      if (
        (this._perlinNoiseGenerator.x > newObstacle.x + newObstacle.image.width + (PLAYER_CART.width / 2 + 5) ||
          this._perlinNoiseGenerator.x < newObstacle.x - (PLAYER_CART.width / 2 + 5)) &&
        (this._perlinNoiseGenerator.y > newObstacle.y + newObstacle.image.height + (PLAYER_CART.height / 2 + 5) ||
          this._perlinNoiseGenerator.y < newObstacle.y - (PLAYER_CART.height / 2 + 5)) &&
        !this._obstacles.some((existingObstacle) => this._obstacleOverlap(existingObstacle, newObstacle))
      ) {
        this._obstacles.push(newObstacle);
      }
    }
  }

  private renderGame() {
    this._context2d.fillStyle = this._gameStyles.backgroundFill;
    this._context2d.fillRect(0, 0, this._canvas.width, this._canvas.height);
    this._context2d.fillStyle = this._gameStyles.streetLineFill;
    this._context2d.fillRect(0, 0, LINE_WIDTH, this._streetStart);
    this._context2d.fillRect(this._canvas.width - LINE_WIDTH, 0, LINE_WIDTH, this._streetStart);

    this._context2d.strokeStyle = this._gameStyles.hitboxStroke;
    this._obstacles
      .concat()
      .reverse()
      .forEach((obstacle) => {
        this._context2d.drawImage(obstacle.image, obstacle.x, obstacle.y);
        if (displayHitboxes) {
          this._context2d.strokeRect(
            obstacle.x + obstacle.hitbox.x,
            obstacle.y + obstacle.hitbox.y,
            obstacle.hitbox.width,
            obstacle.hitbox.height
          );
        }
      });
    if (drawPerlinLine) {
      this._noiseLine
        .concat()
        .reverse()
        .forEach((noise) => {
          this._context2d.fillRect(noise.x, noise.y, 1, 1);
        });
    }

    // Fix for resizing window
    this._playerPosition.y = this._canvas.height - PLAYER_CART_HITBOX.height - 48;
    this._context2d.drawImage(
      PLAYER_CART,
      this._playerPosition.x + this._rumble.x,
      this._playerPosition.y + this._rumble.y
    );
    if (displayHitboxes) {
      this._context2d.strokeRect(
        this._playerPosition.x + PLAYER_CART_HITBOX.x,
        this._playerPosition.y + PLAYER_CART_HITBOX.y,
        PLAYER_CART_HITBOX.width,
        PLAYER_CART_HITBOX.height
      );
    }

    this._context2d.fillStyle = this._gameStyles.scoreTextFill;
    this._context2d.textAlign = 'left';
    this._context2d.font = '20px TVHPro';
    this._context2d.fillText(`Score: ${this.score.toFixed(0)}`, 30, 30);
    if (this.highScore > 0) {
      this._context2d.textAlign = 'left';
      this._context2d.font = '12px TVHPro';
      this._context2d.fillStyle = this._gameStyles.highScoreTextFill;
      this._context2d.fillText(`High Score: ${this.highScore.toFixed(0)}`, 30, 45);
    }
  }

  private _obstacleOverlap(a: Obstacle, b: Obstacle) {
    if (a.x >= b.x + b.image.width || b.x >= a.x + a.image.width) {
      return false;
    }
    // If one rectangle is above other
    if (a.y >= b.y + b.image.height || b.y >= a.y + a.image.height) {
      return false;
    }

    return true;
  }

  private _evaluateHit(obstacle: Obstacle, playerPosition: { x: number; y: number }) {
    const playerCartHitbox: Hitbox = {
      ...PLAYER_CART_HITBOX,
      x: PLAYER_CART_HITBOX.x + playerPosition.x,
      y: PLAYER_CART_HITBOX.y + playerPosition.y,
    };
    const obstacleHitbox: Hitbox = {
      ...obstacle.hitbox,
      x: obstacle.hitbox.x + obstacle.x,
      y: obstacle.hitbox.y + obstacle.y,
    };

    if (
      obstacleHitbox.x >= playerCartHitbox.x + playerCartHitbox.width ||
      playerCartHitbox.x >= obstacleHitbox.x + obstacleHitbox.width
    ) {
      return false;
    }
    // If one rectangle is above other
    if (
      obstacleHitbox.y >= playerCartHitbox.y + playerCartHitbox.height ||
      playerCartHitbox.y >= obstacleHitbox.y + obstacleHitbox.height
    ) {
      return false;
    }

    return true;
  }
}
