export class PerlinNoiseGenerator {
  x: number;
  y: number;
  seed: number;

  // Used for perlin function
  private _width: number;
  private _amp: number;
  private _wl: number;
  private _a: number;
  private _b: number;

  // Used for PseudoRandom
  private _M: number;
  private _A: number;
  private _C: number;
  private _Z: number;

  constructor(width: number, amp: number, wl: number, seed?: number) {
    this._width = width;
    this._initPseudoRandomizer(seed);
    this._initPerlinNoise(amp, wl);
  }

  update(multiplier = 1) {
    const wl = Math.round(this._wl / multiplier);
    if (this.y % wl === 0) {
      this._a = this._b;
      this._b = this._pseudoRand();
      this.x = this._width / 2 + this._a * this._amp;
    } else {
      this.x = this._width / 2 + this._interpolate(this._a, this._b, (this.y % wl) / wl) * this._amp;
    }
    this.y++;
  }

  getRandom() {
    return this._pseudoRand() + 0.5;
  }

  private _initPseudoRandomizer(seed: number) {
    this._M = 4294967296;
    // a - 1 should be divisible by m's prime factors
    this._A = 1664525;
    // c and m should be co-prime
    this._C = 1;
    this._Z = seed || Math.floor(Math.random() * this._M);
    this.seed = this._Z;
  }

  private _pseudoRand() {
    this._Z = (this._A * this._Z + this._C) % this._M;
    return this._Z / this._M - 0.5;
  }

  private _initPerlinNoise(amp: number, wl: number) {
    this.x = this._width / 2;
    this.y = 0;
    this._amp = amp; // amplitude
    this._wl = wl; // wavelength
    this._a = this._pseudoRand();
    this._b = this._pseudoRand();
  }

  private _interpolate(pa: number, pb: number, px: number) {
    const ft = px * Math.PI,
      f = (1 - Math.cos(ft)) * 0.5;
    return pa * (1 - f) + pb * f;
  }
}
