import React from 'react';

import MuiButton from '@material-ui/core/Button';

type Props = {
  tvhVariant?: 'primary' | 'secondary' | 'ghost';
} & React.ComponentProps<typeof MuiButton>;

const Button: React.FC<Props> = ({ tvhVariant, ...props }: Props) => {
  switch (tvhVariant) {
    case 'primary':
      return <MuiButton color="primary" variant="contained" {...props} />;

    case 'secondary':
      return <MuiButton color="default" variant="outlined" {...props} />;

    case 'ghost':
      return <MuiButton color="default" variant="text" {...props} />;

    default:
      return <MuiButton {...props} />;
  }
};

export default Button;
