import { createStyles, makeStyles, useTheme } from '../styles';

const useStyles = makeStyles((theme) =>
  createStyles({
    container: {
      height: '100%',
      display: 'flex',
      flexDirection: 'column',
    },

    canvasContainer: {
      // height: 'calc(100% - 74px)',
      padding: theme.spacing(0, 2),
      flexGrow: 1,
    },

    arrowBoxContainer: {
      flexGrow: 0,
      zIndex: theme.zIndex.drawer,
      width: '334px',
    },

    arrowBox: {
      backgroundColor: theme.palette.common.white,
      boxShadow: theme.shadows[3],
      padding: theme.spacing(2),
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      width: '334px',
      height: '100%',
    },

    arrow: {
      margin: theme.spacing(0, 1),
      height: '42px',
    },

    'arrow--inverted': {
      filter: 'invert(100%)',
    },
  })
);

export interface GameStyles {
  backgroundFill: string;
  streetLineFill: string;
  hitboxStroke: string;
  scoreTextFill: string;
  highScoreTextFill: string;
}

export const useGameStyles = (): GameStyles => {
  const theme = useTheme();
  return {
    backgroundFill: theme.palette.common.white,
    streetLineFill: theme.palette.primary.main,
    hitboxStroke: theme.palette['accent'].promo,
    scoreTextFill: theme.palette.primary.main,
    highScoreTextFill: theme.palette.text.primary,
  };
};

export default useStyles;
