import { Box, Container, Grid } from '@material-ui/core';
import React from 'react';
import { ErrorGame } from '../../ErrorGame';
import useStyles from './ErrorPage.styles';
import * as footer from './footer.png';
import * as tvhLogo from './header.png';

export const ErrorPage: React.FC<{ game?: boolean }> = ({ children, game = true }) => {
  const classes = useStyles();

  return (
    <Box className={classes.root}>
      <Box component="header" className={classes.header}>
        <Container className={classes.container}>
          <img src={tvhLogo.default} alt="TVH" />
        </Container>
      </Box>
      <Container className={classes.innerContainer}>
        <Grid container wrap="nowrap" className={classes.grid}>
          <Box className={classes.contentBox}>{children}</Box>
          {game && <ErrorGame language="en" />}
        </Grid>
      </Container>
      <Box component="footer" className={classes.footer}>
        <Container className={classes.container}>
          <img src={footer.default} alt="TVH" />
        </Container>
      </Box>
    </Box>
  );
};

export default ErrorPage;
