import Box from '@material-ui/core/Box';
import Popper from '@material-ui/core/Popper';
import React, { useEffect, useRef, useState } from 'react';
import { ErrorGameRestartDialog } from '../ErrorGameRestartDialog';
import { ErrorGameStartDialog } from '../ErrorGameStartDialog';
import useStyles, { useGameStyles } from './ErrorGame.styles';
import { FRAME_MIN_TIME, Game } from './game-engine';
import * as leftArrow from './resources/left-arrow.svg';
import * as rightArrow from './resources/right-arrow.svg';

export const ErrorGame: React.FC<{ language: string; seed?: number }> = ({ language, seed }) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);
  const arrowBoxRef = useRef<HTMLDivElement>(null);
  const classes = useStyles();
  const [running, setRunning] = useState<boolean>(false);
  const [score, setScore] = useState<number>(0);
  const [highScore, setHighScore] = useState<number>(0);
  const [dialog, setDialog] = useState<string>('START_GAME');
  const [anchorEl, setAnchorEl] = useState<HTMLDivElement>(null);
  const [leftButtonPressed, setLeftButtonPressed] = useState<boolean>(false);
  const [rightButtonPressed, setRightButtonPressed] = useState<boolean>(false);
  const gameStyles = useGameStyles();
  const game = useRef(new Game(gameStyles, seed));

  const startGame = () => {
    game.current.reset();
    setScore(0);
    setRunning(true);
    setDialog('');
  };

  const endGame = () => {
    setScore(Math.round(game.current.score));
    setHighScore(game.current.highScore);
    setRunning(false);
  };

  useEffect(() => {
    if (!running && score > 0) {
      setDialog('RESTART_GAME');
    }
  }, [running, score]);

  const keyDownListener: EventListener = (event: KeyboardEvent) => {
    switch (event.key) {
      case 'ArrowLeft':
        game.current.arrowLeftDown();
        setRightButtonPressed(false);
        setLeftButtonPressed(true);
        break;
      case 'ArrowRight':
        game.current.arrowRightDown();
        setLeftButtonPressed(false);
        setRightButtonPressed(true);
        break;
    }
  };
  const keyUpListener: EventListener = (event: KeyboardEvent) => {
    switch (event.key) {
      case 'ArrowLeft':
        game.current.arrowLeftUp();
        setLeftButtonPressed(false);
        break;
      case 'ArrowRight':
        game.current.arrowRightUp();
        setRightButtonPressed(false);
        break;
    }
  };

  useEffect(() => {
    canvasRef.current.height = canvasRef.current.offsetHeight;
  }, [canvasRef.current?.offsetHeight]);

  useEffect(() => {
    const context2d = canvasRef.current.getContext('2d');
    canvasRef.current.style.height = '100%';
    canvasRef.current.style.width = '295px';
    setAnchorEl(arrowBoxRef.current);

    let lastFrameTime = 0;
    let requestId: number;

    document.addEventListener('keydown', keyDownListener);
    document.addEventListener('keyup', keyUpListener);

    game.current.init(canvasRef.current, context2d);

    const render = (time: number) => {
      if (time - lastFrameTime < FRAME_MIN_TIME || context2d === null) {
        // skip the frame if the call is too early
        requestId = requestAnimationFrame(render);
        return; // return as there is nothing to do
      }
      lastFrameTime = time; // remember the time of the rendered frame

      if (!game.current.running && game.current.score > 0) {
        endGame();
      }
      // render the frame
      game.current.cycle();
      requestId = requestAnimationFrame(render); // get next farme
    };

    requestId = requestAnimationFrame(render);
    return () => {
      cancelAnimationFrame(requestId);
      document.removeEventListener('keydown', keyDownListener);
      document.removeEventListener('keyup', keyUpListener);
    };
  }, []);

  const open = Boolean(dialog);
  const id = open ? 'transitions-popper' : undefined;

  return (
    <div className={classes.container}>
      <div className={classes.canvasContainer} ref={containerRef}>
        <canvas width="295" height="0" ref={canvasRef}></canvas>
      </div>
      <div className={classes.arrowBoxContainer} ref={arrowBoxRef}>
        <Box className={classes.arrowBox}>
          <img
            className={leftButtonPressed ? classes.arrow + ' ' + classes['arrow--inverted'] : classes.arrow}
            src={leftArrow.default}
            alt="left"
          />
          <img
            className={rightButtonPressed ? classes.arrow + ' ' + classes['arrow--inverted'] : classes.arrow}
            src={rightArrow.default}
            alt="right"
          />
        </Box>
      </div>
      {Boolean(anchorEl) && (
        <Popper
          open={open}
          id={id}
          anchorEl={anchorEl}
          container={anchorEl}
          placement="top-end"
          modifiers={{
            offset: {
              enabled: true,
              offset: '160px, 50px',
            },
          }}
        >
          {dialog === 'START_GAME' && <ErrorGameStartDialog onClick={startGame} />}
          {dialog === 'RESTART_GAME' && (
            <ErrorGameRestartDialog onClick={startGame} score={score} highScore={highScore} />
          )}
        </Popper>
      )}
    </div>
  );
};

export default ErrorGame;
